"use client";
import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import { db } from "@/firebase/firebase";
import Navbar from '../../../components/navbar'
import Footer from '../../../components/footer'
import { collection, doc, getDoc, getDocs, setDoc, addDoc } from "firebase/firestore";
import { addToCart } from "@/app/gio-hang/addtocart";
import { Toaster, toast } from 'react-hot-toast';
import { getAuth, onAuthStateChanged } from "firebase/auth";

export default function ProductDetail() {
  const [review, setReview] = useState("");
  const [reviews, setReviews] = useState([]);
  const [rating, setRating] = useState(0);
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [quantity, setQuantity] = useState(1);
  const [showAddressModal, setShowAddressModal] = useState(false);
  const [address, setAddress] = useState("Ph∆∞·ªùng B√¨nh Chi·ªÉu, TP. Th·ªß ƒê·ª©c, H·ªì Ch√≠ Minh");
  const [tempAddress, setTempAddress] = useState(address);
  const [deliveryTime, setDeliveryTime] = useState("");
  const [userId, setUserId] = useState(null);

  const ratingLabels = ["R·∫•t t·ªá", "T·ªá", "T·∫°m ·ªïn", "T·ªët", "R·∫•t t·ªët"];

  useEffect(() => {
    const auth = getAuth();
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) setUserId(user.uid);
      else setUserId("demoUser");
    });
    return () => unsubscribe();
  }, []);

  useEffect(() => {
    async function fetchProduct() {
      if (!id) return;
      const docRef = doc(db, "products", id);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        setProduct({ id: docSnap.id, ...docSnap.data() });
      }
    }
    fetchProduct();
  }, [id]);

  useEffect(() => {
    async function fetchReviews() {
      if (!id) return;
      const reviewsCol = collection(db, "products", id, "reviews");
      const snapshot = await getDocs(reviewsCol);
      setReviews(snapshot.docs.map(doc => doc.data()).filter(r => !r.hidden));
    }
    fetchReviews();
  }, [id]);

  useEffect(() => {
    const now = new Date();
    const start = new Date(now.getTime() + 2 * 60 * 60 * 1000);
    const end = new Date(now.getTime() + 4 * 60 * 60 * 1000);
    const format = (d) => d.toLocaleTimeString("vi-VN", { hour: "2-digit", minute: "2-digit" });
    setDeliveryTime(`${format(start)} - ${format(end)} h√¥m nay (${now.getDate().toString().padStart(2, "0")}/${(now.getMonth() + 1).toString().padStart(2, "0")})`);
  }, []);

  const handleConfirmAddress = async () => {
    setAddress(tempAddress);
    setShowAddressModal(false);
    if (userId) {
      await setDoc(doc(db, "carts", userId), { address: tempAddress }, { merge: true });
    }
  };

  // ƒê·∫£m b·∫£o truy·ªÅn ƒë√∫ng quantity khi th√™m v√†o gi·ªè h√†ng
  const handleAddToCart = async () => {
    if (!product || !userId) return;
    await addToCart(userId, { ...product, quantity });
    toast.success("ƒê√£ th√™m v√†o gi·ªè h√†ng!");
  };

  const handleStarClick = (star) => {
    if (userId !== "demoUser") setRating(star);
  };

  const handleSubmitReview = async () => {
    if (!review.trim() || rating === 0) {
      toast.error("Vui l√≤ng ch·ªçn s·ªë sao v√† nh·∫≠p n·ªôi dung ƒë√°nh gi√°!");
      return;
    }
    try {
      let userName = "Kh√°ch";
      if (userId) {
        const userDoc = await getDoc(doc(db, "users", userId));
        if (userDoc.exists()) {
          userName = userDoc.data().name || "Kh√°ch";
        }
      }
      await addDoc(collection(db, "products", id, "reviews"), {
        text: review,
        userName,
        date: new Date().toLocaleString("vi-VN"),
        rating,
        hidden: false,
        reported: false,
      });
      setReview("");
      setRating(0);
      toast.success("G·ª≠i ƒë√°nh gi√° th√†nh c√¥ng!");
      const reviewsCol = collection(db, "products", id, "reviews");
      const snapshot = await getDocs(reviewsCol);
      setReviews(snapshot.docs.map(doc => doc.data()).filter(r => !r.hidden));
    } catch (err) {
      toast.error("L·ªói khi g·ª≠i ƒë√°nh gi√°!");
    }
  };

  if (!product) return <div>ƒêang t·∫£i...</div>;

  return (
    <>
      <Navbar />
      <Toaster />
      <main>
        <div className="p-8 text-black bg-gray-100 min-h-screen flex flex-col items-center">
          <h2 className="text-2xl font-bold mb-4">{product.name}</h2>
          <div className="flex gap-8 items-start mb-8">
            <div>
              <div className="border-2 border-blue-400 rounded-lg flex items-center justify-center w-80 h-80 mb-4 bg-white shadow">
                <img
                  src={`${product.imageUrl}`}
                  alt={product.name}
                  className="max-w-full max-h-full object-contain"
                />
              </div>
            </div>
            <div className="bg-white border-2 border-orange-400 rounded-lg p-4 w-80 flex flex-col gap-4 shadow">
              <div>
                <span className="font-semibold">S·ªë l∆∞·ª£ng:</span>
                <div className="flex items-center mt-2">
                  <button
                    className="border px-3 py-1 rounded-l text-lg"
                    onClick={() => setQuantity(q => Math.max(1, q - 1))}
                  >-</button>
                  <span className="mx-4 font-semibold text-lg">{quantity}</span>
                  <button
                    className="border px-3 py-1 rounded-r text-lg"
                    onClick={() => setQuantity(q => q + 1)}
                  >+</button>
                </div>
              </div>
              <div className="flex gap-3">
                <button
                  className="bg-orange-500 hover:bg-orange-600 text-white font-semibold px-3 py-2 rounded flex-1"
                  onClick={handleAddToCart}
                >
                  Th√™m v√†o gi·ªè
                </button>
                <button className="bg-orange-500 hover:bg-orange-600 text-white font-semibold px-3 py-2 rounded flex-1">
                  Mua ngay
                </button>
              </div>
              <div className="bg-gray-50 rounded p-3 text-sm border">
                <div className="font-semibold mb-1">Th√¥ng tin v·∫≠n chuy·ªÉn</div>
                <div className="mb-1 flex items-center gap-2">
                  Giao ƒë·∫øn:{" "}
                  <span className="font-medium">{address}</span>
                  <button
                    className="ml-2 px-2 py-1 border rounded text-xs text-blue-600 hover:bg-blue-50"
                    onClick={() => {
                      setTempAddress(address);
                      setShowAddressModal(true);
                    }}
                  >
                    Thay ƒë·ªïi
                  </button>
                </div>
                <div className="mb-1">
                  Giao h√†ng: <span className="text-green-600 font-semibold">Mi·ªÖn ph√≠</span> - Nh·∫≠n h√†ng:{" "}
                  <span className="font-medium">{deliveryTime}</span>
                </div>
              </div>
            </div>
            {showAddressModal && (
              <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50">
                <div className="bg-white rounded-lg p-6 w-[400px] shadow-lg relative">
                  <button
                    className="absolute top-2 right-3 text-gray-500 text-xl"
                    onClick={() => setShowAddressModal(false)}
                  >√ó</button>
                  <h3 className="font-bold text-lg mb-2">Ch·ªçn ƒë·ªãa ch·ªâ nh·∫≠n h√†ng</h3>
                  <div className="mb-2 text-sm">
                    ƒê·ªãa ch·ªâ ƒëang ch·ªçn: <span className="font-medium">{address}</span>
                  </div>
                  <div className="mb-3">
                    <label className="block text-sm mb-1">Thay ƒë·ªïi ƒë·ªãa ch·ªâ kh√°c</label>
                    <input
                      className="border rounded px-2 py-1 w-full"
                      value={tempAddress}
                      onChange={e => setTempAddress(e.target.value)}
                      placeholder="Nh·∫≠p ƒë·ªãa ch·ªâ m·ªõi..."
                    />
                  </div>
                  <div className="flex justify-end gap-2">
                    <button
                      className="px-3 py-1 rounded border"
                      onClick={() => setShowAddressModal(false)}
                    >Hu·ª∑</button>
                    <button
                      className="px-3 py-1 rounded bg-blue-600 text-white"
                      onClick={handleConfirmAddress}
                    >
                      X√°c nh·∫≠n
                    </button>
                  </div>
                </div>
              </div>
            )}
          </div>
          <div className="bg-white p-6 rounded-lg shadow mt-2 w-[560px]">
            <h3 className="font-semibold text-lg mb-4 border-b pb-2">Th√¥ng tin s·∫£n ph·∫©m</h3>
            <table className="w-full text-sm">
              <tbody>
                <tr>
                  <td className="font-semibold py-2 pr-4 w-1/3">T√™n s·∫£n ph·∫©m:</td>
                  <td className="py-2">{product.name}</td>
                </tr>
                <tr>
                  <td className="font-semibold py-2 pr-4">Danh m·ª•c:</td>
                  <td className="py-2">{product.category}</td>
                </tr>
                <tr>
                  <td className="font-semibold py-2 pr-4">M√¥ t·∫£:</td>
                  <td className="py-2">{product.description}</td>
                </tr>
                <tr>
                  <td className="font-semibold py-2 pr-4">Gi√° s·∫£n ph·∫©m:</td>
                  <td className="py-2 text-red-600 font-bold">{product.price?.toLocaleString()}‚Ç´</td>
                </tr>
                <tr>
                  <td className="font-semibold py-2 pr-4">N·ªïi b·∫≠t:</td>
                  <td className="py-2">{product.featured ? "C√≥" : "Kh√¥ng"}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="bg-white p-6 rounded-lg shadow mt-4 w-[560px]">
            <h3 className="font-semibold text-lg mb-3 border-b pb-2">ƒê√°nh gi√° c·ªßa kh√°ch h√†ng</h3>
            <div className="mb-4">
              <div className="flex items-center gap-3 mb-2 justify-center">
                {[1,2,3,4,5].map((star) => (
                  <span key={star} className="flex flex-col items-center">
                    <svg
                      onClick={() => handleStarClick(star)}
                      width="32" height="32" viewBox="0 0 24 24"
                      fill={star <= rating ? "#FFA500" : "none"}
                      stroke="#FFA500"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="cursor-pointer"
                      style={{ transition: "fill 0.2s" }}
                    >
                      <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"/>
                    </svg>
                    <span className="text-xs mt-1">{ratingLabels[star-1]}</span>
                  </span>
                ))}
              </div>
              <textarea
                className="border rounded px-3 py-2 w-full"
                rows={3}
                value={review}
                onChange={e => setReview(e.target.value)}
                placeholder="Vi·∫øt ƒë√°nh gi√° c·ªßa b·∫°n v·ªÅ s·∫£n ph·∫©m..."
                disabled={userId === "demoUser"}
              />
              <button
                className="mt-2 bg-blue-600 hover:bg-blue-700 text-white font-bold px-4 py-2 rounded"
                onClick={handleSubmitReview}
                disabled={userId === "demoUser"}
              >
                G·ª≠i ƒë√°nh gi√°
              </button>
              {userId === "demoUser" && (
                <div className="text-red-500 mt-2 text-sm">B·∫°n c·∫ßn ƒëƒÉng nh·∫≠p ƒë·ªÉ g·ª≠i ƒë√°nh gi√°.</div>
              )}
            </div>
            <div>
              {reviews.length === 0 && (
                <div className="text-gray-500">Ch∆∞a c√≥ ƒë√°nh gi√° n√†o.</div>
              )}
              {reviews.map((r, idx) => (
                <div
                  key={idx}
                  className={`border-b py-2 ${r.reported ? "border border-red-500 bg-red-50" : ""}`}
                >
                  <div className="flex items-center gap-2">
                    {[1,2,3,4,5].map(star => (
                      <svg key={star} width="18" height="18" viewBox="0 0 24 24"
                        fill={star <= r.rating ? "#FFA500" : "none"}
                        stroke="#FFA500"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      >
                        <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"/>
                      </svg>
                    ))}
                    <span className="text-xs text-gray-500">{ratingLabels[r.rating-1]}</span>
                  </div>
                  <div className="text-sm text-gray-700">{r.text}</div>
                  <div className="text-xs text-gray-400">{r.userId} - {r.date}</div>
                  {r.reported && (
                    <div className="text-red-600 font-semibold mt-1">
                      B√¨nh lu·∫≠n n√†y vi ph·∫°m
                    </div>
                  )}
                </div>
              ))}
           </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
}